body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body{
    margin: 0;
    font-family: Arial;
    background: #171c30;
}

.HomeLayout {
    display: flex;
    margin: 0 auto;
    min-height: 100vh;
}
.Media {
  cursor: pointer;
  margin-right: 15px;
  display: inline-block;
  width: 260px;
  vertical-align: top;
}

.Media:hover .Media-cover {
  box-shadow: 0 0 100px rgba(0,0,0,.3);
}

.Media-cover {
  -webkit-transition: .3s;
  transition: .3s;
  box-shadow: 0 0 5px rgba(0,0,0,.3);
  height: 160px;
}

.Media-image {
  object-fit: cover;
}

.Media-title {
  margin: 5px 0 0 0;
  color: #44546b;
  white-space: normal;
}

.Media-author {
  font-size: 12px;
  margin: 5px 0 0 0;
}
.Playlist {
    overflow-x: auto;
    /*width: 500px;*/
    white-space: nowrap;
    /*display: flex;*/
    /*flex-wrap: wrap;*/
    /*height: 500px;*/
    /*background: red;*/
    overflow-y: hidden;
    padding-bottom: 80px;
  }
.Category {
    margin-bottom: 1em;
  }
  
  .Category-title {
    text-transform: uppercase;
    font-size: 20px;
    color: #3f546c;
    margin: 0 0 20px 0;
    letter-spacing: 2px;
    font-weight: bold;
  }
  .Category-description {
    color: #888b8e;
    font-weight: 300;
    margin: 0;
    font-size: 14px;
  }
.Categories {
    overflow: hidden;
    padding: 20px 0 20px 20px;
    background: white;
  }
.Related {

    width: 300px;
  }
.Modal {
    max-width: 768px;
    width: 100%;
    margin: 0 auto;
    position: fixed;
    left: 0;
    right: 0;
    top: 100px;
    background:  white;
    padding: 20px;
    border-radius: 20px;
    box-shadow: 0 0 10px rgba(0,0,0,.4);
  }
  
  .Modal-close {
    width: 50px;
    height: 50px;
    position: absolute;
    right: -25px;
    top: -25px;
    border-radius: 50%;
    border: none;
    background: #171c30;
    color: white;
    cursor: pointer;
  }
  
  .Modal-close:after {
    content: "X";
    font-weight: bold;
    font-size: 30px;
  }
